import axios, { AxiosInstance, AxiosRequestConfig, AxiosResponse } from "axios";
import * as qs from "qs";
import { base } from "../conf";
import { message } from "antd";
import { hideLoading, showLoading } from "./loading";
import { TOKEN_KEY } from "utils/local-storage";
// 
const showStatus = (status: number) => {
  let message: string;
  switch (status) {
    case 400:
      message = "(400)";
      break;
    case 401:
      message = "(401)";
      break;
    case 403:
      message = "(403)";
      break;
    case 404:
      message = "(404)";
      break;
    case 408:
      message = "(408)";
      break;
    case 500:
      message = "(500)";
      break;
    case 501:
      message = "(501)";
      break;
    case 502:
      message = "(502)";
      break;
    case 503:
      message = "(503)";
      break;
    case 504:
      message = "(504)";
      break;
    case 505:
      message = "HTTP(505)";
      break;
    default:
      message = `(${status})!`;
  }
  return `${message}`;
};
let loadingCount: number = 0; // loadingCountloading
// map pending
const pending = new Map();
// ...
const addPending = (config: AxiosRequestConfig) => {
  const url = [
    config.method,
    config.url,
    qs.stringify(config.params),
    qs.stringify(config.data),
  ].join("&");
  config.cancelToken =
    config.cancelToken ||
    new axios.CancelToken((cancel) => {
      if (!pending.has(url)) {
        //  pending 
        pending.set(url, cancel);
      }
    });
};
// ...
const removePending = (config: AxiosRequestConfig) => {
  const url = [
    config.method,
    config.url,
    qs.stringify(config.params),
    qs.stringify(config.data),
  ].join("&");
  if (pending.has(url)) {
    //  pending 
    const cancel = pending.get(url);
    cancel(url);
    pending.delete(url);
  }
};
//  pending
export const clearPending = () => {
  for (const [url, cancel] of pending) {
    cancel(url);
  }
  pending.clear();
};
export enum Methods {
  POST = "POST",
  GET = "GET",
  DELETE = "DELETE",
  PUT = "PUT",
  PATCH = "PATCH",
}

// TODO   ...
const defaultConf = {
  isShowLoading: true,
  method: Methods.GET,
};

interface IConf {
  isShowLoading: boolean;
  method: Partial<Methods>;
}
export default (conf: IConf = defaultConf) => {
  /**
   * 1. service
   * 2.   // https://developer.huawei.com/consumer/cn/forum/topic/0202545523679410023 // TODO 
   * 3. 
   * 4. 
   * 5.  
   * 6.  ...
   */
  // @ts-ignore
  const { baseURL } = base[process.env.NODE_ENV];
  const service: AxiosInstance = axios.create({
    // @ts-ignore
    baseURL, // baseUrl e.g: http://localhost:8080/api/
    headers: {
      "X-Requested-With": "XMLHttpRequest",
      // 'Content-Type': 'application/x-www-form-urlencoded',
      "Content-Type": "application/json",
    },
    withCredentials: true, // 
    timeout: 30000, // 
    validateStatus() {
      // TODO async-awaitrejectresolve
      return true;
    },
    transformRequest: [
      (data) => {
        // data
        data = JSON.stringify(data);
        return data;
      },
    ],
    transformResponse: [
      (data) => {
        if (typeof data === "string" && data.startsWith("{")) {
          // 
          data = JSON.parse(data);
        }
        return data;
      },
    ],
  });
  // service.defaults.headers.post['Content-Type'] = 'application/json'
  service.interceptors.request.use(
    (config: AxiosRequestConfig) => {
      if (config.headers!.isLoading !== false) {
        showLoading(loadingCount);
      }
      loadingCount++;
      removePending(config); // 
      addPending(config); //  pending 
      let token = localStorage.getItem(TOKEN_KEY); // token
      if (token) {
        config.headers!.Authorization = `${token}`;
      }
      // :blob
      // if (config.url!.includes('pur/contract/export')) {
      //   config.headers!['responseType'] = 'blob'
      // }
      // 'Content-Type'
      // if (config.url!.includes('pur/contract/upload')) {
      //   config.headers!['Content-Type'] = 'multipart/form-data'
      // }

      return Object.assign({}, config, conf);
    },
    (error) => {
      // Loading
      if (error.config.headers.isLoading !== false) {
        hideLoading(loadingCount);
      }
      // 
      error.data = {};
      error.data.msg = "";
      return Promise.resolve(error);
    }
  );

  service.interceptors.response.use(
    // 
    async (response: AxiosResponse) => {
      if (response.config.headers!.isLoading !== false) {
        hideLoading(loadingCount);
      }
      loadingCount--;
      removePending(response); // 
      const status = response.status;
      let msg = "";
      if (status < 200 || status >= 300) {
        // http
        msg = showStatus(status);
        if (typeof response.data === "string") {
          response.data = { msg };
        } else {
          response.data.msg = msg;
        }
      }
      if (+response.data.status === 0) {
        return response.data;
      } else if (+response.data.status === 1) {
        await message.warning(response.data.msg);
        return response.data.msg;
      }
      // 
      return response;
    },
    async (error) => {
      if (error.config.headers!.isLoading !== false) {
        hideLoading(loadingCount);
      }
      if (axios.isCancel(error)) {
        console.log("repeated request: " + error.message);
      } else {
        // 
        error.data = {};
        error.data.msg = "";
        await message.warning(error.data.msg);
      }
      return Promise.reject(error);
    }
  );

  return service;
};
