import { useRoutes, RouteObject, Outlet } from "react-router-dom";
import { Suspense, lazy, memo } from "react";
import Main from "../pages/main";
import AuthRequire from "./auth-require";
import Loading from "../components/loading";
const Login = lazy(() => import("../pages/login"));
const Register = lazy(() => import("../pages/register"));
const PageNotFound = lazy(() => import("../pages/404"));

// home
const Home = lazy(() => import("../pages/main/home"));

// w3c
const Html = lazy(() => import("../pages/main/w3c/html"));
const Css = lazy(() => import("../pages/main/w3c/css"));
const Js = lazy(() => import("../pages/main/w3c/js"));

// vue
const VueBasic = lazy(() => import("../pages/main/vue/basic"));
const VueDirectives = lazy(() => import("../pages/main/vue/directives"));
const VueRouter = lazy(() => import("../pages/main/vue/router"));
const VueWeex = lazy(() => import("../pages/main/vue/weex"));
const VueElementUI = lazy(() => import("../pages/main/vue/element"));

// react
const ReactBasic = lazy(() => import("../pages/main/react/basic"));
const ReactHooks = lazy(() => import("../pages/main/react/hooks"));
const ReactRouter = lazy(() => import("../pages/main/react/router"));
const ReactRedux = lazy(() => import("../pages/main/react/redux"));
const ReactAntd = lazy(() => import("../pages/main/react/antd"));

// 

const Article = lazy(() => import("../components/article"));

// 
const routes: RouteObject[] = [
  {
    path: "/",
    element: <AuthRequire element={<Main />} />,
    children: [
      {
        element: <Home />,
        // indexpath
        index: true,
      },
      {
        path: "w3c",
        element: <Outlet />,
        children: [
          {
            path: "html",
            element: <Outlet />,
            children: [
              {
                index: true,
                element: <Html />,
              },
              {
                path: "article/:key",
                element: <Article />,
              },
            ],
          },
          {
            path: "css",
            element: <Css />,
          },
          {
            path: "js",
            element: <Js />,
          },
        ],
      },
      {
        path: "vue",
        element: <Outlet />,
        children: [
          {
            path: "basic",
            element: <VueBasic />,
          },
          {
            path: "directives",
            element: <VueDirectives />,
          },
          {
            path: "router",
            element: <VueRouter />,
          },
          {
            path: "weex",
            element: <VueWeex />,
          },
          {
            path: "element",
            element: <VueElementUI />,
          },
        ],
      },
      {
        path: "react",
        element: <Outlet />,
        children: [
          {
            path: "basic",
            element: <ReactBasic />,
          },
          {
            path: "hooks",
            element: <ReactHooks />,
          },
          {
            path: "router",
            element: <ReactRouter />,
          },
          {
            path: "redux",
            element: <ReactRedux />,
          },
          {
            path: "antd",
            element: <ReactAntd />,
          },
        ],
      },
    ],
  },
  {
    path: "/login",
    element: <Login />,
  },
  {
    path: "/register",
    element: <Register />,
  },
  {
    path: "*",
    element: <PageNotFound />,
  },
];

// 
const generateRouter = (routes: RouteObject[]) => {
  return routes.map((item) => {
    if (item.children) {
      item.children = generateRouter(item.children);
    }
    item.element = <Suspense fallback={<Loading />}>{item.element}</Suspense>;
    return item;
  });
};

const Router = () => useRoutes(generateRouter(routes));
export default memo(Router);

